@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=cyrillic);
.header .title {
  width: 87px;
  height: 28px;
  font-weight: bold;
  font-size: 20px;
  line-height: 28px; }

.header .welcome {
  display: flex;
  flex-direction: column;
  margin-top: 78px; }
  .header .welcome__hey {
    width: 100%;
    height: 44px;
    font-weight: bold;
    font-size: 32px;
    line-height: 44px; }
  .header .welcome__lets-try {
    height: 44px;
    font-weight: 500;
    font-size: 24px;
    line-height: 44px; }

.not-found {
  display: flex;
  justify-content: center;
  margin: 30% auto;
  font-size: 1.5rem; }

body {
  position: relative;
  max-width: 1440px;
  color: #333333;
  background-color: #FFFFFF;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  margin: 0 auto; }

.wrapper {
  width: 100%;
  min-height: 900px;
  display: flex;
  border: 1px solid black; }

.main-container {
  display: flex;
  flex-direction: column;
  width: 66.7%;
  padding: 40px 140px 140px 140px; }

.favourite-container {
  width: 33.3%;
  background-color: #F8F8F8; }

.error-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30% auto;
  font-size: 1.5rem; }

